@value White, LightGrey, LightPink from '../../common/styles.css';

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3.8em;
  padding-bottom: 1.2em;
  height: 100%;
  width: 100%;
  overflow-y: auto;
}

.content {
  border: 2px solid LightPink;
  margin-bottom: 2em;
  padding: 2em;
  background-color: White;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.title {
  text-align: center;
  font-size: 1.6em;
  margin-bottom: 1.2em;
}

.writing {
  font-size: 1.1em;
  white-space: pre-line;
}

.writer {
  font-size: 1em;
  color: LightGrey;
  margin-top: 1.4em;
}

.noWriting {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 1.2em;
}
